// ProtectedRoute.jsx
import React from 'react';
import { Navigate } from 'react-router-dom';

const ProtectedRoute = ({ children }) => {
  const isAuthenticated = localStorage.getItem('token'); // Kim tra token

  if (!isAuthenticated) {
    // Nu khng c token, chuyn hng n trang ng nhp
    return <Navigate to="/login" />;
  }

  return children; // Nu  xc thc, hin th component con
};

export default ProtectedRoute;
